body{background-color:#fefde9}*{margin:0;padding:0;box-sizing:border-box}h1,h2,h3,h4{font-family:"Playfair Display",serif}p,button{font-family:"Red Hat Display",sans-serif}button{border:none;cursor:pointer}button:focus{outline:0}@media screen and (min-width: 1400px){#root{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}}

