.container-dango-selection{background-color:#fefde9;width:100%;height:400px;overflow-x:hidden;border-top-left-radius:2px;border-top-right-radius:2px}.container-dango-selection .container-catagories{width:100%;height:20%;min-height:50px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;overflow-x:auto;border-bottom:3px solid #433633;border-bottom-left-radius:5px;border-bottom-right-radius:5px}.container-dango-selection .container-catagories .top{width:100%;height:50%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.container-dango-selection .container-catagories .top .catagory-btn{width:100%;height:100%;font-size:1rem;border-top-left-radius:3px;border-top-right-radius:3px}.container-dango-selection .container-catagories .bot{width:100%;height:50%;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.container-dango-selection .container-catagories .bot .catagory-btn{width:100%;height:100%}.container-dango-selection .container-catagories .catagory-btn{font-size:0.85rem;letter-spacing:1px;background-color:#fefde9;color:#433633;font-weight:700;transition:all 250ms ease-in-out}.container-dango-selection .container-catagories .catagory-btn:hover{background-color:#fae8ae}.container-dango-selection .container-catagories .catagory-btn.active{background-color:#f8e9c8}.container-dango-selection .container-dangos{width:100%;height:310px;overflow-y:scroll;display:grid;grid-template-columns:repeat(2, 1fr);transition:all 500ms ease-in-out;grid-template-rows:auto;grid-gap:5px;gap:5px;border-top-left-radius:5px;border-top-right-radius:5px;-webkit-justify-content:center;justify-content:center}@media screen and (min-width: 481px){.container-dango-selection{height:500px;overf