.container-dango-effect{width:100%;height:100%;background-color:#272d41;min-height:100px;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;border-radius:2px;font-size:0.95rem;border:2px solid #615e5e}.container-dango-effect .top{width:90%;height:30%;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:center;align-items:center;color:#fff;font-size:0.85rem}.container-dango-effect .top h3{font-family:"Red Hat Display",sans-serif}.container-dango-effect .bot{width:90%;height:60%;display:-webkit-flex;display:flex;-webkit-justify-content:flex-start;justify-content:flex-start;-webkit-align-items:flex-start;align-items:flex-start;color:#F4D35E}@media screen and (min-width: 599px){.container-dango-effect{font-size:1.15rem}.container-dango-effect .top{font-size:1rem}}

.container-dango-stick{height:100%;width:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column;margin:0.5rem 0 0 0}.container-dango-stick .container-stick-section{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.container-dango-stick .container-stick-section .stick{width:45%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.container-dango-stick .container-stick-section .stick img{max-width:140px;max-height:140px}.container-dango-stick .container-stick-section .effect{width:50%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.container-dango-stick .dango{width:auto;height:auto;position:absolute;z-index:10}@media screen and (min-width: 481px){.container-dango-stick{width:85%}.container-dango-stick .stick img{max-width:175px;max-height:175px}}@media screen and (min-width: 769px){.container-dango-stick{margin-top:1rem;width:75%}.container-dango-stick .stick img{max-width:250px;max-height:250px}}@media screen and (min-width: 1025px){.container-dango-stick .container-stick-section .stick img{max-width:175px;max-height:175px}}

