.container-app{width:100%;height:100%;max-width:1400px;background-color:#fefde9}.container-app .container-dango-selection-stick{width:100%;height:100%;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;-webkit-flex-direction:column;flex-direction:column}

